<template>
  <div class="sign-in-out">
    <h2>扫码 签到/签退</h2>
    <q-r-code :codeUrl="codeUrl" :loading="loading" />
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, onBeforeUnmount } from 'vue'
import { getCode } from '@/service/sign-in-out'
import QRCode from '@/components/qr-code/src/QRCode.vue'

export default defineComponent({
  components: { QRCode },
  name: 'SignInOut',
  setup() {
    const codeUrl = ref('')
    const loading = ref(true)
    getCode()
      .then((res) => {
        codeUrl.value = window.URL.createObjectURL(res)
        loading.value = false
      })
      .catch((err) => {
        console.log(err)
      })
    const interval = window.setInterval(() => {
      getCode()
        .then((res) => {
          codeUrl.value = window.URL.createObjectURL(res)
        })
        .catch((err) => {
          console.log(err)
        })
    }, 30 * 1000)

    onBeforeUnmount(() => {
      // console.log('清除循环定时器')
      window.clearInterval(interval)
    })
    return {
      codeUrl,
      loading
    }
  }
})
</script>

<style lang="less" scoped>
.sign-in-out {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
</style>
