import { dfRequest } from '../request'
import { ElMessage } from 'element-plus'

export function getCode() {
  return dfRequest.post<Blob, Blob>({
    url: '/api/getcode/',
    responseType: 'blob',
    cancelGlobalInterceptor: true,
    interceptors: {
      responseInterceptor(res) {
        if (res.size) {
          ElMessage({
            message: '已刷新二维码',
            type: 'success'
          })
        }
        return res
      }
    }
  })
}
